<template>
    <div id="schoolMap">
        <div class="school">
            <div class="title">学校地图</div>
            <el-button class="back" type="primary" size="small" @click="back()">返回</el-button>
            <img :src='schoolmap' alt="学校地图">
        </div>
    </div>
</template>

<script>
export default {
    name:'schoolMap',
    data(){
        return{
            schoolmap: 'static/img/schoolMap.jpg'
        }
    },
    methods: {
        back(){
            this.$router.go(-1)
        }
    },
}
</script>

<style scoped>
    #schoolMap{
        width: 100%;
        height: 100%;
        padding: 30px 0;
        position: relative;
    }
    img{
        width: 100%;
        height: 100%;
    }
    .title{
        font-size: 26px;
        font-weight: 900;
        text-align: center;
        margin-bottom: 10px;
    }
    .back{
        position: absolute;
        right: 5%;
        top: 32px;
    }
</style>



